import React, {Component} from 'react';
import Info from '../../../../components/molecules/App/resume/info.jsx';
import Skor from '../../../../components/molecules/App/resume/skor.jsx';

import './index.scss';
class Resume extends Component {

	componentDidMount(){
	  let skor = document.getElementsByClassName('skor-indikator');
	  let ketSkor = document.getElementsByClassName('ket-skor');
	  let skorInd = document.getElementsByClassName('skor-indikator');

	  [...skor].forEach(function (key, i) {
	   let pixel = key.getAttribute('data-skor');
	   ketSkor[i].innerHTML = pixel;
	   // ketSkor[i].style.left = pixel;
	   skorInd[i].style.width = pixel;
	})

	}

	render() {
		return (
		    <div className="resume" id="resume">
		    
		    	<div className="resume-container">
		    		
		    		<h2 id="pendidikan">Pendidikan</h2>

		    		<Info kode="pendidikan" icon='fa-university' tahun='2015-2020' ket1="Teknik Informatika" ket2="Universitas Muhammadiyah Parepare" />
		    		<Info kode="pendidikan" icon='fa-university' tahun='2012-2015' ket1="Teknik Komputer dan Jaringan" ket2="SMK Negeri 5 SIDRAP" />
		    		<Info kode="pendidikan" icon='fa-university' tahun='2009-2012' ket2="SMP Negeri 2 Manisa" />
		    		<Info kode="pendidikan" icon='fa-university' tahun='2003-2009' ket2="SDN Negeri 2 Benteng" />
		    		<Info kode="pendidikan" icon='fa-university' tahun='2002-2003' ket2="TK Aisyah Benteng" />

		    		<h2 id="pengalaman">Pengalaman</h2>
		    		<Info kode="pengalaman" icon='fa-flask' tahun='2020' ket1="Fresh Graduate"  />
		    		<Info kode="pengalaman" icon='fa-flask' tahun='Agustus - September 2018' ket1="Program Magang" ket2="Media Center Universitas Muhammadiyah Parepare"  />
		    		<Info kode="pengalaman" icon='fa-flask' tahun='Juli - September 2013' ket1="Program PKL" ket2="ANAF Komputer Parepare"  />
		    		
		    		<h2 id="keterampilan">Keterampilan</h2>
		    		
		    		<div className="keterampilan-value">
		    		
		    		 <Skor nama="HTML" skor="90%" />	
		    		 <Skor nama="CSS" skor="75%" />	
		    		 <Skor nama="Javascript" skor="80%" />	
		    		 <Skor nama="JQuery" skor="80%" />	
		    		 <Skor nama="Boostrap" skor="90%" />	
		    		 <Skor nama="Framework 7" skor="80%" />
		    		 <Skor nama="ReactJs" skor="60%" />
		    		 
		    		 <Skor nama="Node Js" skor="60%" />
		    		 <Skor nama="PHP" skor="80%" />	
		    		 <Skor nama="CodeIgniter" skor="70%" />	
		    		 <Skor nama="Apache Cordova" skor="90%" />	
		    		 <Skor nama="React Native" skor="60%" />	
		    		 <Skor nama="Android Studio" skor="50%" />

		    		 <Skor nama="Firebase WEB" skor="75%" />

		    		 <Skor nama="Java Netbeans" skor="60%" />	
		    		 <Skor nama="Github" skor="70%" />	
		    		 <Skor nama="Photoshop" skor="40%" />	
		    		 <Skor nama="Office" skor="80%" />	
		    			
		    		</div>	
		    	</div>
		    </div>
		);
	}

}

export default Resume;