import React, {Component} from 'react';
import './index.scss';

class Hero extends Component {

	componentDidMount(){
		let text = ['full stack developer','blogger','freelance', 'programmer'];

		this.typingText(text);
	}

	typingText = (txt, i=0, a=0) => {
		let delay = 150;

		if (a <txt.length) {

			if (i < txt[a].length) {
				document.querySelector('.typing').innerHTML += txt[a][i];
				i++;
			}else{
				let txtNow = document.querySelector('.typing').innerHTML;
				if (i === txt[a].length) {
					delay = 1000;
					i++;
				}else{
					let txtSub = txtNow.substring(0, txtNow.length-1);
					document.querySelector('.typing').innerHTML = txtSub;
					if (txtSub === "") {
						delay = 1000;
						i=0;
						a++;
					}

				}
			}
		}else{
			a=0;
			i=0;			
		}
		setTimeout(()=>{
			this.typingText(txt, i, a);
		}, delay);
	}

	render(){
		return (
		    <div className="hero-wrap">
		    	<div className="overlay"></div>

		    	<div className="container">
		    		<p className="hey">hey! saya</p>
		    		<p className="nama">asri</p>
		    		<p className="seorang">saya seorang <span>&raquo;</span><span className="typing"></span><span>&laquo;</span></p>
		    	</div>

		    </div>
		);
	}
}

export default Hero;