import React, {Fragment} from 'react';

const Info = (props) => {
	return (
		<Fragment>
		<div className={`resume-value ${props.kode}`}>
			<div className="container-icon">
					<span className="icon">
						    <i className={`fa ${props.icon}`}></i>
						  </span>
			</div>
			<div className="container-description">
				<p>{props.tahun}</p>
				<h3>{props.ket1}</h3>
				<p>{props.ket2}</p>
			</div>
		</div>
		<hr/>
		</Fragment>
	)
}

export default Info;