import React, {Component} from 'react';
import './index.scss';
import Info from '../../../../components/molecules/App/about/info.jsx';

class About extends Component {
	render() {
		return (
		    <div className="about" id="about">
		    	<div className="foto item"></div>
		    	<div className="profile item">
		    		<h1>Tentang Saya</h1>
		    		<p>Saya Seorang Full Stack Developer, Blogger dan Freelance. Saya bisa HTML, CSS, Bootstrap, PHP, Codeigniter, Javascript, Jquery, React JS, React Native, Apache Cordova, MySQL Database And Firebase.</p>

	    			<ul className="about-info">
		            	<Info index={'nama'} value={'Asri'}/>
		            	<Info index={'Tanggal Lahir'} value={'15 Desember, 1996'}/>
		            	<Info index={'Alamat'} value={'Jln. Poros Pare Rappang, Panreng'}/>
		            	<Info index={'Kode Pos'} value={'91652'}/>
		            	<Info index={'Email'} value={'azrhycool@gmail.com'}/>
		            	<Info index={'Telepon'} value={'+628533528916'}/>
		            </ul>

		    	</div>
		    </div>
		);

	}
}

export default About;