import React from 'react';

const Skor = (props) => {
	return (
		<div className="ket-container">
			<p className="ket-name">{props.nama}
			 <span className="ket-skor"></span>
			</p>
			<div className="skor-container"><div className="skor-indikator" data-skor={props.skor}></div></div>
		</div>
	)
}

export default Skor;