import React from 'react';

const Card = (props) => {
	return (
		<div className="card-contact">
			<span className="icon">
				<i className={`fa ${props.icon}`}></i>
			</span>
			<p className="contact-name">{props.name}</p>
			<p className="contact-ket"><a target="_blank" rel="noopener noreferrer" href={props.href}>{props.ket}</a></p>
		</div>
	)
}

export default Card;