import React, {Component} from 'react';
import './index.scss';

class Nav extends Component {
	
	showNav = () =>{
		let nav = document.querySelector('.topnav');
		nav.classList.toggle('responsive');
	}


	pageScroll = (props) =>{
		let href = props.target.getAttribute('href');
		let tujuan = document.querySelector(href);
		
		window.scrollTo({top: tujuan.offsetTop-150, left: 0, behavior: 'smooth' });

		// document.documentElement.scrollTop = tujuan.offsetTop-150;
	}

	componentDidMount(){
		let nav = document.querySelector('#myTopnav');
		let navHref = document.querySelector('.nav-href');

		let hero = document.querySelector('.hero-wrap .container')
		let about = document.querySelector('#about');
		let pendidikan = document.querySelector('#pendidikan');
		let pengalaman = document.querySelector('#pengalaman');
		let keterampilan = document.querySelector('#keterampilan');
		let portofolio = document.querySelector('#portofolio');
		let contact = document.querySelector('#contact');
		let footer = document.querySelector('#footer');

		about.style.marginTop = (hero.offsetHeight+30)+'px';

		const animation = (kode) => {
			let kodeA = document.querySelectorAll(kode);

			[...kodeA].forEach(function(e,i){
				setTimeout(()=>{
					e.classList.add('muncul');
				}, 250 * (i+1));
			})
		}

		const navDinamis = (kode) =>{
			document.querySelector('.active').classList.remove('active');
			document.querySelector(kode).classList.add('active');
		}
	


		document.addEventListener("scroll", function(e){
			let scrollNow = document.documentElement.scrollTop;

			//untuk navigasi
			if (scrollNow >= hero.offsetHeight/3) {
				nav.style.backgroundColor='black';
				nav.style.color='white';
				navHref.classList.add('white-a');
			}else{
				nav.removeAttribute('style');
				navDinamis('.nav-home');
				navHref.classList.remove('white-a');
			}
			//untuk about
			if (scrollNow >= about.offsetTop-300) {
				animation('.about .item');
				navDinamis('.nav-about');
			}

			 //untuk pendidikan
			if (scrollNow >= pendidikan.offsetTop-300) {
				navDinamis('.nav-resume');
				animation('.resume-container .pendidikan');
			}				 
			//untuk pengalaman
			if (scrollNow >= pengalaman.offsetTop-300) {
				animation('.resume-container .pengalaman');
			}

			//untuk keterampilan
			if (scrollNow >= keterampilan.offsetTop-300) {
				animation('.resume-container .keterampilan-value .ket-container');
			}	 

			//untuk portofolio
			if (scrollNow >= portofolio.offsetTop-300) {
				navDinamis('.nav-portofolio');
				animation('.portofolio-container .item');
			}

						
			//untuk contact
			if (scrollNow >= contact.offsetTop-300) {
				navDinamis('.nav-contact');
				animation('.contact-container .card-contact');
			}			

			//untuk footer
			if (scrollNow >= footer.offsetTop-300) {
				animation('.social-media div a');
				animation('.tentang .ket');
				animation('.alamat .alamat-value');
				animation('.footer footer');
			}

		

		})
	}

	render(){
		return (
		    <div className="topnav" id="myTopnav">
		    	<div className="logo-web">
		    		<p><span>A</span>sri</p>
		    	</div>	
				<div className="nav-href">
				  <a className="nav-home active" onClick={
				  	(e)=>{
				  		e.preventDefault();
				  		this.pageScroll(e);
				  	}
				  } href="#home">Home</a>
				  <a className="nav-about" onClick={
				  	(e)=>{
				  		e.preventDefault();
				  		this.pageScroll(e);
				  	}
				  } href="#about">About</a>
				  <a className="nav-resume" onClick={
				  	(e)=>{
				  		e.preventDefault();
				  		this.pageScroll(e);
				  	}
				  } href="#resume">Resume</a>
				  <a className="nav-portofolio" onClick={
				  	(e)=>{
				  		e.preventDefault();
				  		this.pageScroll(e);
				  	}
				  } href="#portofolio">portofolio</a>
				  <a className="nav-contact" onClick={
				  	(e)=>{
				  		e.preventDefault();
				  		this.pageScroll(e);
				  	}
				  } href="#contact">Contact</a>
				</div>
				<div className="nav-button" onClick={this.showNav}>
				  <span className="icon">
				    <i className="fa fa-bars"></i>
				  </span>
				</div>
		    </div>
		);
	}
}

export default Nav;